import { Socket, io } from 'socket.io-client';
import FollowedVacationModel from '../Models/FollowedVacationModel';
import VacationModel from '../Models/VacationModel';
import { addFollowedVacationAction, deleteFollowedVacationAction } from '../Redux/FollowedVacationsState';
import { followedVacationsStore, vacationsStore } from '../Redux/Store';
import { addVacationAction, deleteVacationAction, updatePartialVacationAction, updateVacationAction } from '../Redux/VacationsState';
import config from '../Utils/Config';


class SocketIoService {

    private socket: Socket;

    public connect(): void {

        // Connect to socket server:
        this.socket = io(config.urls.socketServer);

        // Listen to adding a  vacation  by admin:
        this.socket.on("admin-add-vacation", (vacation: VacationModel) => {
            vacationsStore.dispatch(addVacationAction(vacation));
            console.log(vacation);
        });

        // Listen to updating a vacation follow by user:
        this.socket.on("user-update-followVacation", (vacation: VacationModel) => {
            vacationsStore.dispatch(updatePartialVacationAction(vacation));
        });
        // Listen to updating a vacation by admin:
        this.socket.on("admin-update-vacation", (vacation: VacationModel) => {
            vacationsStore.dispatch(updatePartialVacationAction(vacation));
        });

        // Listen to deleting a vacation by admin:
        this.socket.on("admin-delete-vacation", (id: number) => {
            vacationsStore.dispatch(deleteVacationAction(id));
        });


        // Listen to adding a follow vacation by user:
        this.socket.on("user-add-followVacation", (vacation: VacationModel) => {
            vacationsStore.dispatch(updatePartialVacationAction(vacation));
        });
        // this.socket.on("user-add-followVacation", (followVacation: FollowedVacationModel) => {
        //     // followedVacationsStore.dispatch(addFollowedVacationAction(followVacation));
        //     console.log(followVacation);
        //     const newFollowedVacation: FollowedVacationModel = new FollowedVacationModel();
        //     newFollowedVacation.userId = +followVacation.userId;
        //     newFollowedVacation.vacationId = +followVacation.vacationId;
        //     console.log(newFollowedVacation);

        //     // followedVacationsStore.dispatch(addFollowedVacationAction(newFollowedVacation));
        // });


        // Listen to deleting a follow vacation by user:
        this.socket.on("user-delete-followVacation", (vacation: VacationModel) => {
            vacationsStore.dispatch(updatePartialVacationAction(vacation));
            // console.log("User Un-follow vacation - User ID: "+userId+" User ID: "+vacationId);
        });

    }

    public disconnect(): void {
        this.socket.disconnect();
    }

}

const socketIoService = new SocketIoService();

export default socketIoService;



